html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Rubik", sans-serif;
  font-size: 1.8rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: hsl(229deg, 31%, 21%);
  font-weight: 500;
}

button {
  font-weight: 500;
}

p {
  color: hsl(233deg, 5%, 36%);
  font-size: 1.8rem;
  font-weight: 400;
  line-height: 1.5;
}

h2 {
  font-size: 2.5rem;
  font-weight: 500;
}

h3 {
  font-size: 2.3rem;
  font-weight: 500;
}

ul {
  list-style: none;
  padding: 0;
}

.img-fluid {
  width: 100%;
}

.header {
  padding: 3.5rem 3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
@media (min-width: 992px) {
  .header {
    padding: 3.5rem 12.5rem;
  }
}
.header .logo {
  z-index: 11;
}
.header .navegacion {
  background-color: hsl(229deg, 31%, 21%);
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  transform: translateY(-100%);
  z-index: 10;
}
@media (min-width: 768px) {
  .header .navegacion {
    background-color: hsl(0deg, 0%, 100%);
    position: relative;
    align-items: flex-end;
    height: auto;
    transform: translateY(0%);
  }
}
.header .navegacion .socials-nav img {
  margin-right: 3rem;
}
.header .navegacion .socials-nav img:last-of-type {
  margin-right: 0;
}
@media (min-width: 768px) {
  .header .navegacion .socials-nav {
    display: none;
  }
}
.header .navegacion.active {
  transform: translateY(0%);
}
.header .open-menu {
  z-index: 11;
}
@media (min-width: 768px) {
  .header .open-menu {
    display: none;
  }
}

.enlaces {
  display: flex;
  flex-direction: column;
  width: 80%;
}
.enlaces li {
  text-align: center;
  text-transform: uppercase;
  padding: 2rem 0;
  border-top: 1px solid hsl(233deg, 5%, 36%);
}
.enlaces li a {
  color: hsl(0deg, 0%, 100%);
  font-weight: 400;
  font-size: 2rem;
  letter-spacing: 0.25rem;
}
.enlaces li a.login {
  display: block;
  font-weight: 500;
  border: 2px solid hsl(0deg, 0%, 100%);
  border-radius: 0.5rem;
  padding: 1.5rem;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .enlaces {
    margin: 0;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
  }
  .enlaces li {
    padding: 0;
    border: none;
  }
  .enlaces li a {
    color: hsl(229deg, 31%, 21%);
    font-size: 1.4rem;
    letter-spacing: 2px;
    margin-right: 3rem;
  }
  .enlaces li a:hover {
    color: #fa5757;
  }
  .enlaces li a.login {
    margin: 0;
    border: 2px solid transparent;
    background-color: #fa5757;
    color: hsl(0deg, 0%, 100%);
    font-size: 1.7rem;
    font-weight: 500;
    border-radius: 0.8rem;
    padding: 1.2rem 4rem;
  }
  .enlaces li a.login:hover {
    border: 2px solid #fa5757;
    background-color: hsl(0deg, 0%, 100%);
    color: #fa5757;
  }
}

.content-main {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .content-main {
    flex-direction: row;
    align-items: center;
  }
  .content-main .images-main {
    flex: 0 1 50%;
    order: 2;
  }
  .content-main .info-main-cont {
    flex: 0 1 50%;
    order: 1;
  }
}
.content-main .images-main {
  width: 100%;
  position: relative;
  margin: 3rem 0;
}
.content-main .images-main .img-hero {
  position: relative;
  z-index: 1;
}
.content-main .images-main .bg {
  background-color: #5368df;
  position: absolute;
  right: 0;
  bottom: 0;
  width: 79%;
  height: 75%;
  border-bottom-left-radius: 25%;
  z-index: 0;
}

.info-main {
  width: 90%;
  margin: 0 auto;
}
.info-main h1 {
  text-align: center;
  font-size: 3rem;
  font-weight: 500;
  text-transform: capitalize;
  margin: 2rem 0;
  line-height: 1.4;
}
.info-main p {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 400;
  line-height: 1.5;
}
@media (min-width: 768px) {
  .info-main {
    width: 100%;
    padding-left: 2.8rem;
  }
  .info-main h1, .info-main p {
    text-align: start;
  }
}
@media (min-width: 992px) {
  .info-main {
    padding-left: 12.5rem;
  }
  .info-main h1 {
    font-size: 3.5rem;
    max-width: 35rem;
  }
  .info-main p {
    max-width: 50rem;
  }
}

.get-btn {
  width: 90%;
  margin: 3rem auto;
  display: flex;
  justify-content: center;
}
.get-btn a {
  text-align: center;
  margin-right: 2rem;
  padding: 1.5rem 2rem;
  font-size: 1.6rem;
  border-radius: 0.8rem;
}
.get-btn a:last-of-type {
  margin-right: 0;
}
.get-btn .btn-blue {
  background-color: #5368df;
  color: hsl(0deg, 0%, 100%);
  border: 2px solid transparent;
}
.get-btn .btn-blue:hover {
  cursor: pointer;
  background-color: hsl(0deg, 0%, 100%);
  border: 2px solid #5368df;
  color: #5368df;
}
.get-btn .btn-white {
  box-shadow: 1px 1px 1px 2px hsl(230deg, 10%, 89%);
  border: 2px solid transparent;
}
.get-btn .btn-white:hover {
  cursor: pointer;
  background-color: hsl(0deg, 0%, 100%);
  border: 2px solid hsl(229deg, 31%, 21%);
  color: hsl(229deg, 31%, 21%);
  box-shadow: none;
}
@media (min-width: 768px) {
  .get-btn {
    justify-content: start;
    width: 100%;
    padding-left: 2.8rem;
  }
}
@media (min-width: 992px) {
  .get-btn {
    padding-left: 12.5rem;
  }
}

.features-section {
  margin: 8rem 0;
  padding: 1rem 0;
}
@media (min-width: 768px) {
  .features-section {
    margin: 0;
  }
}

.info-features {
  width: 80%;
  margin: 5rem auto;
}
.info-features h2 {
  text-align: center;
}
.info-features p {
  text-align: center;
}
@media (min-width: 768px) {
  .info-features p {
    margin: 0 auto;
    max-width: 50rem;
  }
}

.btn-features {
  width: 80%;
  margin: 0 auto;
}
@media (min-width: 992px) {
  .btn-features {
    width: 55%;
  }
}
@media (min-width: 768px) {
  .btn-features ul {
    display: grid;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 0;
  }
}
.btn-features ul li {
  text-align: center;
  border-top: 1px solid hsl(0deg, 0%, 81%);
}
.btn-features ul li:last-of-type {
  border-bottom: 1px solid hsl(0deg, 0%, 81%);
}
@media (min-width: 768px) {
  .btn-features ul li {
    border-top: none;
    border-bottom: 1px solid hsl(0deg, 0%, 81%);
  }
}
.btn-features ul li button {
  border: transparent;
  background-color: transparent;
  color: hsl(233deg, 5%, 36%);
  padding: 2rem 0;
  font-weight: 500;
  position: relative;
}
.btn-features ul li button::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background: #5368df;
  z-index: 1;
  transform: scaleX(0);
  transform-origin: center;
  transition: transform 0.5s ease-in-out;
}
.btn-features ul li button:hover {
  cursor: pointer;
}
.btn-features ul li button:hover::before {
  transform: scaleX(1);
  transform-origin: center;
}
.btn-features ul li button.active-ft {
  color: hsl(229deg, 31%, 21%);
}
.btn-features ul li button.active-ft::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background: #fa5757;
  transform: scale(1);
  transform-origin: center;
}
@media (min-width: 768px) {
  .btn-features ul li button {
    width: 100%;
  }
}

@media (min-width: 768px) {
  .content-tab {
    display: flex;
    align-items: center;
  }
  .content-tab .images-ft {
    flex: 0 1 50%;
  }
  .content-tab .info-tabs {
    flex: 0 1 50%;
  }
}

.images-ft {
  width: 100%;
  position: relative;
  margin: 3rem 0;
}
.images-ft .img-ft {
  position: relative;
  z-index: 1;
  padding: 3rem;
}
@media (min-width: 992px) {
  .images-ft .img-ft {
    padding: 3rem 3rem 6rem 12.5rem;
  }
}
.images-ft .bg {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 79%;
  height: 75%;
  border-bottom-right-radius: 25%;
  z-index: 0;
}

.info-tabs {
  width: 80%;
  margin: 0 auto;
}
.info-tabs h2 {
  text-align: center;
}
.info-tabs p {
  text-align: center;
}
@media (min-width: 768px) {
  .info-tabs {
    width: 100%;
    padding: 0 2.8rem;
  }
  .info-tabs h2, .info-tabs p {
    text-align: start;
  }
}
@media (min-width: 992px) {
  .info-tabs {
    padding: 0 12.5rem;
  }
  .info-tabs h2 {
    font-size: 3.5rem;
  }
  .info-tabs p {
    max-width: 45rem;
  }
}

.pricing-section {
  padding: 4.5rem 0;
}
.pricing-section .main-pricing {
  width: 80%;
  margin: 0 auto;
}
.pricing-section .main-pricing h2 {
  text-align: center;
}
.pricing-section .main-pricing p {
  text-align: center;
}
@media (min-width: 992px) {
  .pricing-section .main-pricing p {
    max-width: 55rem;
    margin: 0 auto;
  }
}

.download-section {
  padding: 4rem;
}
.download-section .chrome,
.download-section .firefox,
.download-section .opera {
  box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
  margin: 6rem 0;
}
.download-section .chrome .top-info,
.download-section .firefox .top-info,
.download-section .opera .top-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  text-align: center;
}
.download-section .chrome .top-info h3,
.download-section .firefox .top-info h3,
.download-section .opera .top-info h3 {
  margin: 4rem 0 0 0;
}
.download-section .chrome .top-info p,
.download-section .firefox .top-info p,
.download-section .opera .top-info p {
  margin: 1rem;
}
.download-section .chrome .btn-download,
.download-section .firefox .btn-download,
.download-section .opera .btn-download {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}
.download-section .chrome .btn-download .btn-dl,
.download-section .firefox .btn-download .btn-dl,
.download-section .opera .btn-download .btn-dl {
  background-color: #5368df;
  color: hsl(0deg, 0%, 100%);
  text-align: center;
  padding: 1.5rem 3rem;
  border-radius: 0.8rem;
  border: 2px solid transparent;
}
.download-section .chrome .btn-download .btn-dl:hover,
.download-section .firefox .btn-download .btn-dl:hover,
.download-section .opera .btn-download .btn-dl:hover {
  cursor: pointer;
  background-color: hsl(0deg, 0%, 100%);
  border: 2px solid #5368df;
  color: #5368df;
}
@media (min-width: 768px) {
  .download-section {
    width: 90%;
    display: flex;
    justify-content: center;
    height: 45rem;
    padding: 0;
    margin: 4rem auto;
  }
  .download-section .chrome {
    margin: 0 3rem 0 0;
    align-self: flex-start;
  }
  .download-section .firefox {
    margin: 0 3rem 0 0;
    align-self: center;
  }
  .download-section .opera {
    margin: 0;
    align-self: flex-end;
  }
}

.faq {
  width: 80%;
  margin: 0 auto;
}
@media (min-width: 992px) {
  .faq {
    width: 35%;
  }
}
.faq .top-faq {
  margin: 5rem 0;
}
.faq .top-faq h2, .faq .top-faq p {
  text-align: center;
}

.enlace a {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.enlace a:hover {
  color: #fa5757;
}
.enlace img {
  width: 3.7rem;
}
.enlace .info-faq p {
  text-align: start;
  margin-bottom: 1rem;
}

.enlace {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: 1rem 0;
  border-bottom: 0.2px solid hsl(0deg, 0%, 81%);
}
.enlace .info-faq {
  max-height: 0;
}
.enlace .info-faq.expand {
  max-height: 50rem;
}

.btn-faq {
  display: flex;
  justify-content: center;
  margin: 5rem 0;
}
.btn-faq button {
  border: 2px solid transparent;
  background-color: #5368df;
  color: hsl(0deg, 0%, 100%);
  padding: 1.5rem 3rem;
  border-radius: 0.8rem;
}
.btn-faq button:hover {
  cursor: pointer;
  background-color: hsl(0deg, 0%, 100%);
  border: 2px solid #5368df;
  color: #5368df;
}

.contact-section {
  background-color: #5368df;
  padding: 5rem 0;
}
.contact-section .form-contact {
  width: 80%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contact-section .form-contact {
    width: 50%;
  }
}
@media (min-width: 992px) {
  .contact-section .form-contact {
    width: 30%;
  }
}
.contact-section .form-contact h2 {
  font-size: 1.6rem;
  font-weight: 400;
  text-transform: uppercase;
  color: hsl(0deg, 0%, 100%);
  letter-spacing: 5px;
  text-align: center;
}
.contact-section .form-contact form label {
  display: block;
  color: hsl(0deg, 0%, 100%);
  font-size: 2.5rem;
  font-weight: 500;
  text-align: center;
}
.contact-section .form-contact form input[type=email] {
  width: 100%;
  height: 5rem;
  border: transparent;
  background-color: hsl(0deg, 0%, 100%);
  color: hsl(229deg, 31%, 21%);
  margin: 3rem 0 0 0;
  padding: 0 2rem;
  border-radius: 0.8rem;
  outline: none;
  font-size: 1.6rem;
}
.contact-section .form-contact form input[type=email].error-mail {
  border: 1px solid #fa5757;
}
.contact-section .form-contact form input[type=email]::placeholder {
  color: hsl(233deg, 5%, 36%);
}
.contact-section .form-contact form .error {
  margin: -1rem 0 0 0;
  background-color: #fa5757;
  color: hsl(0deg, 0%, 100%);
  padding: 1rem 1rem 0 1rem;
  border-bottom-left-radius: 0.8rem;
  border-bottom-right-radius: 0.8rem;
}
.contact-section .form-contact form .exito {
  margin: 0;
  text-align: center;
  font-weight: 500;
  text-transform: capitalize;
  color: hsl(0deg, 0%, 100%);
  padding: 0 1rem;
}
.contact-section .form-contact form input[type=submit] {
  width: 100%;
  height: 5rem;
  margin: 2rem 0;
  border: transparent;
